.homeBody{
  h1{
    font-size: 20px !important;
  }
  .connexion{
    width: 80%;
    margin: auto;
    padding-top: 10px;
    form{
      div:nth-child(1){
        display: flex;
        flex-direction: column;
        align-items: center;
        input{
          margin-bottom: 25px;
          padding: 10px;
          border-radius: 5px;
          outline: none;
          border: 1px solid gray;
        }
      }
      div:nth-child(2){
        display: flex;
        justify-content: space-evenly;
      }
    }
  }
}